import React from 'react'
import './Section.css'

function Section(props) {

    const title = props.title === undefined ? <></> : <div className="it-wallet-section-title">{props.title}</div>;

    return (
        <div className="it-wallet-section">
            {title}
            <div className="it-wallet-section-body">{props.children}</div>
        </div>
    )
}

export default Section