import React from 'react'
import config from './../config.json'
import './Header.css'
import { useNavigate } from 'react-router-dom'

function Header() {

    const navigate = useNavigate()

    return (
        <div className="it-wallet-header">
            <div className="it-wallet-title">{config.website.title}</div>
            <div className="it-wallet-settings">
                <span className="it-wallet-network-panel">{config.network.name}</span>
                <span className="it-wallet-logout-button fa fa-right-from-bracket" onClick={() => {delete localStorage.privateKey; navigate(0)}}></span>
            </div>
        </div>
    )
}

export default Header