import React, {useState} from 'react'
import {ethers} from 'ethers'
import './Dashboard.css'
import config from './../config.json'

import Header from '../components/Header'
import Section from '../components/Section'
import CoinDashboard from '../components/CoinDashboard'

export default function Homepage() {
    
    const [balance, setBalance] = useState(0)
    const [publicAddress, setPublicAddress] = useState('')
    const [fiat, setFiat] = useState(0)

    const getFiat = async () => {
        await fetch('https://api.binance.com/api/v3/avgPrice?symbol=AVAXUSDT')
        .then(response => response.json())
        .then(result => {
            setFiat(parseFloat(result.price))
        })
    }

    // Let's get data
    const provider = new ethers.JsonRpcProvider(config.network.rpc)
    const wallet = new ethers.Wallet(localStorage.getItem('privateKey'))

    provider.getBalance(wallet.address).then(balance => {
        setPublicAddress(wallet.address)
        setBalance(ethers.formatEther(balance))
        getFiat()
    })
    
    return (
        <div className="main">
            <Header />
            <div className="content">
                <Section>
                    <span className="it-wallet-account-name">Account 1</span>
                    <span className="it-wallet-account-public-address">{publicAddress.substring(0, 5) + "..." + publicAddress.substring(38, 42)}</span>
                </Section>
                <CoinDashboard
                    balance={balance}
                    symbol={config.network.symbol}
                    fiatBalance={(fiat*balance).toFixed(2)}
                    fiatSymbol="USD"
                />
                <Section>
                    <span className="it-wallet-copyright-text">&copy; ilhamitugral</span>
                </Section>
            </div>
        </div>
    )
}
