import React, {useState} from 'react'
import { useNavigate } from 'react-router-dom'
import {ethers} from 'ethers'
import './Login.css'

import Section from '../components/Section'

function Login() {

    const navigate = useNavigate()
    const [privateKey, setPrivateKey] = useState('')
    const [errorText, setErrorText] = useState('')

    const checkLogin = () => {
        if(privateKey.length === 64) {
            const wallet = new ethers.Wallet(privateKey)
            localStorage.privateKey = wallet.address === undefined ? '' : privateKey
            setErrorText('')
            navigate(0)
        }else {
            setPrivateKey('')
            setErrorText('Invalid wallet address.')
        }
    }

    return (
        <div className="login-panel">
            <div className="login-area">
                <div className="content">
                    <Section title="Login to IT Wallet">
                        <p className="it-wallet-login-error-text">{errorText}</p>
                        <input type="password" className="it-wallet-input-field" onChange={e => setPrivateKey(e.target.value)} value={privateKey} placeholder="Private Key"/>
                        <button className="it-wallet-button-field" onClick={() => checkLogin()}>Login</button>
                    </Section>
                    <Section>
                        <p className="it-wallet-copyright-text">This project not collecting any data.</p>
                        <p className="it-wallet-copyright-text">Private key information keeps in <strong>localStorage</strong> on your browser.</p>
                    </Section>
                    <br/>
                    <p className="it-wallet-copyright-text">&copy; ilhamitugral</p>
                </div>
            </div>
        </div>
    )
}

export default Login